.searchReposTable {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr auto;

}

.searchReposDivRequest {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-direction: column;
        flex-direction: column;
}

#reposRequest {
    grid-row: 1/2;
    grid-column: 1/2;
}

#reposFilter {
    grid-row: 1/2;
    grid-column: 2/3;
}


